
.specifications {
  position: fixed;
  left: 0;
  top: 100%;
  bottom: 0;
  z-index: 14000;
  width: 100%;
  height: 70%;
  background: #fff;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition: all 0.1s linear;
  transition: all 0.1s linear;
}
.specifications .goods {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  padding: 10px;
  border-bottom: solid 1px rgba(204,204,204,0.3);
}
.specifications .goods .goodsInfo {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  padding-left: 10px;
  -ms-flex-item-align: end;
      align-self: flex-end;
}
.specifications .goods .goodsInfo .price {
  color: #f00;
  font-size: 16px;
  padding-bottom: 2px;
}
.specifications .goods .goodsInfo .goodsId {
  font-size: 14px;
  color: #8e8e93;
}
.specifications .goods .colseMask {
  padding: 2px 0 10px 15px;
  display: table;
}
.specifications .GoodsSpecifications {
  padding: 0 10px;
}
.specifications .GoodsSpecifications .title {
  margin: 5px 0 10px 0;
  color: #666;
}
.specifications .GoodsSpecifications .category {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.specifications .GoodsSpecifications .category span {
  border: solid 1px #ccc;
  border-radius: 3px;
  padding: 4px 8px;
  margin: 0 15px 10px 0;
  font-size: 12px;
}
.specifications .GoodsSpecifications .category .active {
  color: #f00;
  border: solid 1px rgba(255,0,0,0.4);
}
.specifications .Determine {
  background: #ff4b52;
  color: #fff;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 10px 0;
  text-align: center;
  font-size: 18px;
  letter-spacing: 10px;
}


