
.Myagreement .page-content {
  background: #f7f7f8;
}
.Myagreement .page-content .content-block {
  height: calc(100% - 0px);
  width: calc(100% - 25px);
  position: absolute;
  overflow-y: scroll;
  margin: 2px 0;
  padding: 0;
  display: block;
  font-size: 14px;
  line-height: 24px;
  margin-left: 15px;
}
.Myagreement .page-content .content-block h1 {
  text-align: center;
  color: #1f2124;
  font-size: 18px;
  font-weight: 600;
  margin: 20px;
}
.Myagreement .page-content .content-block h3 {
  color: #1f2124;
  font-size: 18px;
}
.Myagreement .page-content .content-block p {
  color: #383a3f;
  font-size: 16px;
  line-height: 1.6em;
  margin: 1em 6px 1rem 0;
}


