
@import "../static/css/reset.css";
@import "../static/css/framework7.ios.min.css";
@import "../static/css/public.css";
@import "../static/css/app.css";
#app {
  position: relative;
  height: 100%;
  width: 100%;
}
#loading {
  width: 100%;
  height: 100%;
  position: fixed;
  background: rgba(197,197,197,0.5);
  left: 0;
  top: 0;
  z-index: 6666;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.views {
  z-index: 6000;
}
.tabbar-labels {
  position: absolute;
  background: #fff;
  font-size: 13px;
  width: 100%;
  height: 50px;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #929292;
  left: 0;
  bottom: 0;
  z-index: 5001;
}
.tabbar-labels .toolbar-inner {
  width: 100%;
  height: 100%;
  padding: 4px 0;
  overflow: hidden;
}
.tabbar-labels .toolbar-inner .tab-link {
  padding-top: 1px;
  position: relative;
}
.tabbar-labels .toolbar-inner .tab-link .iconfont {
  font-size: 20px;
}
.tabbar-labels .toolbar-inner .tab-link .cartNum {
  min-width: 10px;
  height: 10px;
  line-height: 10px;
  padding: 1px;
  text-align: center;
  border-radius: 10px;
  color: #f00;
  font-size: 10px;
  border: solid 1px #f00;
  background: #fff;
  position: absolute;
  left: 52%;
  top: 0;
}
.tabbar-labels .toolbar-inner .active {
  color: #6c6;
}
.vux-pop-out-enter-active,
.vux-pop-out-leave-active,
.vux-pop-in-enter-active,
.vux-pop-in-leave-active {
  will-change: transform;
  -webkit-transition: all 350ms;
  transition: all 350ms;
  height: 100%;
  top: 0;
  position: absolute;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-perspective: 1000;
          perspective: 1000;
}
.vux-pop-out-enter {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
.vux-pop-out-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.vux-pop-in-enter {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.vux-pop-in-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}


